@import "./_base";
@import "./_variable";

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Noto+Sans+TC:wght@400;700&display=swap');

* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif; }

img {
  width: 100%;
  vertical-align: top; }

header {
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
  figure.logo {
    width: 145px; } }

footer {
  width: 100%;
  height: 30px;
  background-image: url(../images/bg02.png);
  background-size: 1920px; }

.num {
  font-family: 'Lato','Noto Sans TC', sans-serif; }

html {
  font-size: 20px;
  article {
    section.section01 {
      background-color: $yellow;
      .wrapper {
        max-width: 1580px;
        width: 100%;
        padding: 30px 15px;
        margin: auto;
        box-sizing: border-box;
        figure.title {
          max-width: 1050px;
          margin: auto; }
        .box {
          width: 100%;
          padding: 15px 0;
          box-sizing: border-box;
          .owl-item {
            &:nth-child(even) {
              padding-top: 60px; } }
          .owl-dots {
            text-align: center;
            .owl-dot {
              width: 16px;
              height: 16px;
              margin: 0 5px;
              border-radius: 50%;
              background-color: #fff;
              &.active {
                background-color: $orange; } } } } }
      @include rwd(768px) {
        .wrapper {
          padding: 30px;
          figure.title {
            // max-width: 1050px
            width: 100%; }
          .box {
            padding: 15px 30px;
            .owl-item {
              &:nth-child(even) {
                padding-top: 0px; } } } } } }
    section.section02,section.section04 {
      width: 100%;
      padding: 80px 0;
      background-image: url(../images/bg01.png);
      background-size: 34px;
      background-position: -10px -10px;
      .wrapper {} }
    section.section03 {
      background-color: $pink;
      .wrapper {
        width: 100%;
        margin: auto;
        figure.title {
          width: 630px;
          margin: auto;
          position: relative;
          top: -40px; }
        .container {
          padding: 60px 0;
          text-align: center;
          .circle {
            @include dib;
            position: relative;
            @include before {
              width: 100%;
              padding-bottom: 100%;
              background-color: #ffa1a1;
              border: 2px solid #db7777;
              border-radius: 50%;
              @include pstc5;
              z-index: 0; }
            .info {
              padding: 0 50px;
              position: relative;
              z-index: 1;
              .txt1 {
                width: 110px;
                margin: 0 auto 10px;
                font-size: 1.1rem;
                line-height: 1.8rem;
                color: #db7777;
                background-color: #fff;
                border-radius: 0.9rem; }
              .txt2 {
                font-size: 2rem;
                line-height: 1.3;
                color: #706061; }
              .txt3 {
                font-size: 2rem;
                line-height: 1.3;
                color: #706061;
                font-weight: bold; } } }
          figure.arrow {
            width: 28px;
            margin: 0 25px;
            @include dib; }
          .box {
            @include dib;
            .item {
              padding: 0 60px 0 120px;
              margin-bottom: 60px;
              background-color: #edfcff;
              border: 1px solid #4eaac6;
              line-height: 4.8rem;
              border-radius: 2.4rem;
              position: relative;
              figure.pic {
                position: absolute;
                top: 50%;
                right: calc(100% - 120px); }
              .txt {
                font-size: 1.8rem;
                font-weight: bold;
                color: #0f8fb6; }
              &:nth-child(1) {
                figure.pic {
                  width: 145px;
                  transform: translate(-13%,-40%); } }
              &:nth-child(2) {
                figure.pic {
                  width: 105px;
                  transform: translate(-15%,-53%); } }
              &:nth-child(3) {
                figure.pic {
                  width: 125px;
                  transform: translate(-13%,-71%); } } } } } }
      @include rwd(1080px) {
        .wrapper {
          figure.title {
            // width: 630px
 }            // top: -40px
          .container {
            // padding: 60px 0
            .circle {
              &:before {}
              .info {
                padding: 0 30px;
                .txt1 {
                  width: 100px;
                  margin: 0 auto 5px;
                  font-size: 1rem;
                  // line-height: 1.8rem
 }                  // border-radius: 0.9rem
                .txt2,.txt3 {
                  font-size: 1.6rem;
 } } }                  // line-height: 1.3
            figure.arrow {
              width: 20px;
              margin: 0 15px; }
            .box {
              .item {
                padding: 0 30px 0 120px;
                margin-bottom: 30px;
                line-height: 4rem;
                border-radius: 2rem;
                figure.pic {
 }                  // right: calc(100% - 120px)
                .txt {
                  font-size: 1.4rem; }
                &:nth-child(1) {
                  figure.pic {
                    // width: 145px
 } }                    // transform: translate(-13%,-40%)
                &:nth-child(2) {
                  figure.pic {
                    // width: 105px
 } }                    // transform: translate(-15%,-53%)
                &:nth-child(3) {
                  figure.pic {
                    // width: 125px
 } } } } } } }                    // transform: translate(-13%,-71%)
      @include rwd(768px) {
        .wrapper {
          padding-bottom: 60px;
          figure.title {
            width: 80%;
            top: 0;
            transform: translateY(-40%); }
          .container {
            padding: 0;
            .circle {
              max-width: 160px;
              margin: 50px auto;
              display: block;
              &:before {}
              .info {
                // padding: 0 20px
                .txt1 {
                  width: 60px;
                  margin: 0 auto 5px;
                  font-size: .8rem;
                  line-height: 1.2rem;
                  border-radius: 0.4rem; }
                .txt2,.txt3 {
                  font-size: 1rem;
 } } }                  // line-height: 1.3
            figure.arrow {
              display: block;
              width: 20px;
              margin: auto;
              transform: rotate(90deg); }
            .box {
              width: 90%;
              margin: 1rem auto;
              display: flex;
              justify-content: space-between;
              .item {
                width: 30%;
                padding: 0;
                margin-bottom: 0px;
                margin-top: 30px;
                line-height: 3rem;
                border-radius: 1rem;
                figure.pic {
                  top: 0;
                  right: 0;
                  left: 0;
                  margin: auto; }
                .txt {
                  font-size: 1rem; }
                &:nth-child(1) {
                  figure.pic {
                    width: 60px;
                    transform: translate(0%,-30%); } }
                &:nth-child(2) {
                  figure.pic {
                    width: 40px;
                    transform: translate(0%,-50%); } }
                &:nth-child(3) {
                  figure.pic {
                    width: 45px;
                    transform: translate(0%,-50%); } } } } } } } }
    section.section05 {
      width: 100%;
      padding: 15px 0;
      background-image: url(../images/bg02.png);
      background-size: 1920px;
      .wrapper {
        max-width: 1200px;
        width: 95%;
        margin: auto;
        figure.title {
          max-width: 506px;
          width: 90%;
          margin: auto; }
        .form-box {
          width: 100%;
          padding: 0 65px 50px;
          margin: 70px auto;
          border: 2px dotted $green;
          font-size: 1.4rem;
          color: $gray;
          font-weight: bold;
          background-color: #fff;
          border-radius: 1.5rem;
          box-sizing: border-box;
          figure.sub-title {
            max-width: 343px;
            margin: auto;
            transform: translateY(-35%); }
          .remind {
            margin-bottom: 50px;
            text-align: center;
            br {
              display: none; } }
          .row {
            display: flex;
            margin-bottom: 35px;
            .row-label {
              width: 350px;
              p {
                line-height: 3.4rem; } }
            .radio-box {
              width: 100px;
              label {
                line-height: 3.4rem;
                display: block;
                input[type="radio"] {
                  width: 38px;
                  @include dib; }
                p {
                  @include dib; } } }
            .row-content {
              flex: 1;
              // width: calc(100% - 350px)
              input[type="text"],input[type="phone"] {
                width: 100%;
                line-height: 3.4rem;
                border-radius: 1.7rem;
                border: 1px solid #d4d1d1;
                box-sizing: border-box; } }
            input[type="checkbox"] {
              width: 38px; }
            p.txt {
              a {
                color: #e85549; } }
            &.align-center {
              align-items: center; } }
          .btn {
            width: 186px;
            margin: auto;
            font-size: 1.8rem;
            line-height: 3.2rem;
            color: #f27777;
            border: 2px solid #f27777;
            background-color: #f7e0e0;
            border-radius: calc(1.6rem + 2px);
            text-align: center;
            cursor: pointer;
            transition: .3s;
            @include hover {
              color: #fff;
              background-color: #f27777; } } } }
      @include rwd(768px) {
        // padding: 15px 0
        // background-size: 1920px
        .wrapper {
          figure.title {}
          .form-box {
            padding: 0 30px 30px;
            margin: 50px auto;
            font-size: 1rem;
            border-radius: 1rem;
            figure.sub-title {
              width: 70%; }
            .remind {
              margin-bottom: 15px;
              br {
                display: block; } }
            .row {
              flex-wrap: wrap;
              margin-bottom: 20px;
              .row-label {
                width: 100%;
                p {
                  line-height: 2rem; } }
              .radio-box {
                width: 80px;
                label {
                  line-height: 2rem;
                  input[type="radio"] {
 }                    // width: 38px
                  p {} } }
              .row-content {
                input[type="text"],input[type="phone"] {
                  line-height: 2rem;
                  border-radius: 1rem; } }
              input[type="checkbox"] {
                width: 20px; }
              p.txt {
                font-size: 0.75rem;
                a {} } }
            .btn {
              width: 120px;
              font-size: 1rem;
              line-height: 2rem;
              border-radius: calc(1rem + 2px); } } } } }
    section.section06 {
      padding-bottom: 30px;
      .wrapper {
        max-width: 1200px;
        width: 100%;
        padding: 0 15px;
        margin: auto;
        box-sizing: border-box;
        figure.title {
          max-width: 343px;
          width: 90%;
          margin: auto;
          transform: translateY(-40%); }
        .remind {
          font-size: 1.4rem;
          font-weight: bold;
          color: #787372;
          text-align: center; }
        .box {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          text-align: center;
          .item {
            max-width: 250px;
            width: 100%;
            padding-top: 50px;
            text-align: center;
            display: inline-block;
            p.txt {
              padding: 0 20px;
              font-weight: 1.4rem;
              line-height: 2.4rem;
              font-weight: bold;
              color: $green02;
              border-radius: 1.2rem;
              border: 1px solid $green02;
              display: inline-block; }
            figure.pic {
              width: 100%;
              margin: 10px 0; } } }
        .nav-bar {
          text-align: center;
          p {
            cursor: pointer;
            @include dib; }
          .now {
            color: $green02;
            @include dib; }
          .page {
            color: $green02;
            @include dib; } } } } } }
